	module.exports = {"pathContext":{"posts":[{"html":"<h3>Getting a letsencrypt certificate on a host with shell access</h3>\n<p>Use the Certbot ACME client documented in the <a href=\"https://certbot.eff.org\">certbot pages</a>.</p>\n<h3>Decoding and evaluating the generated TLS certificate</h3>\n<ul>\n<li>\n<p>paste the certificate contained in the <code>cert.pem</code> file found somewhere under the\n<code>/etc/letsencrypt</code> directory into the form provided\nin the <a href=\"https://www.sslshopper.com/certificate-decoder.html\">SSLHopper pages</a>.\nThe same test can be done with the <code>chain.pem</code> or with\nthe <code>fullchain.pem</code> file.</p>\n</li>\n<li>\n<p>paste the domain of the site to be evaluated into the form found in\nthe SSL Server test page of the <a href=\"https://www.ssllabs.com/ssltest/\">www.ssllabs.com/ssltest/</a>.</p>\n</li>\n<li>\n<p>another similar test is available at <a href=\"https://www.digicert.com/help\">www.digicert.com/help</a>.</p>\n</li>\n</ul>","id":"/home/nicolas/Documents/workshop/src/pages/2020-11-01-transport-layer-security/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2020-11-01T21:15:34.235Z","path":"/transport-layer-security","title":"Transport Layer Security","excerpt":"","tags":["TLS","web security","TLS certificate"]}}],"tagName":"TLS certificate"}}

